header{
    height: 8rem;
    width: 100%;
    background-color: $blueT;
    backdrop-filter: blur(1rem);
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    z-index: 200;
    top: 0;
    .logo {
        height:70px;
        width:auto;
        margin-left: 5rem;
        img {
            max-height:100%;
            max-width:100%;
        }
    }
    .navegacion {
        justify-content: space-between;
        a {
            font-size: 2.5rem;
            margin: 0rem 2rem;
            color: $white;
            &:hover {
                transform: scale(1.2);
                color: $gray;
                transition: all 300ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
                box-shadow: 0 0 1rem $whiteT;
                border-radius: .5rem;
            }
        }
    }
}
.container-icon{
    position: relative;
    margin-right: 3rem;
}

.count-products{
    position: absolute;
    top: 55%;
    right: 0;

    background-color: $white;
    color: $black;
    width: 25px;
    height: 25px;

    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
}
#contador-productos{
    font-size: 12px;
}
.container-cart-products{
    position: absolute;
    top: 50px;
    right: 0;
    background-color: $grayC;
    width: 400px;
    z-index: 1;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.20);
    border-radius: 10px;
    
}
.cart-product{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 30px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.20);

}
.info-cart-product{
    display: flex;
    justify-content: space-between;
    flex: 0.8;
}

.titulo-producto-carrito{
    font-size: 20px;
}

.precio-producto-carrito{
    font-weight: 700;
    font-size: 20px;
    margin-left: 10px;
}

.cantidad-producto-carrito{
    font-weight: 400;
    font-size: 20px;
}

.icon-close{
    width: 25px;
    height: 25px;
}

.icon-close:hover{
    stroke: red;
    cursor: pointer;
}

.cart-total{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 0;
    gap: 20px;
}

.cart-total h3{
    font-size: 20px;
    font-weight: 700;
}

.total-pagar{
    font-size: 20px;
    font-weight: 900;
}

.hidden-cart{
    display: none;
}



