.hero-somos {
    width: 100vw;
    height: 50vh;
    background-image: url(/build/img/somos-hero.jpg);
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    position: relative;
    overflow: hidden;
    text-align: center;
    .color {
        padding-top: 16rem;
        width: 100%;
        height: 100%;
        background-color: $blueT;
        
        .textos {
            h1,p {
                color: $white;
            }
            h1 {
                letter-spacing: 3rem;
                font-size: 5.5rem;
                font-weight: $black-f;
            }
            p {
                letter-spacing: 1rem;
                font-size: 2rem;
            }
        }
    }
}
.info {
    .galery-s{
        height: 33rem;
        display: flex;
        picture{
            width: 100%;
            height: 100%;
            margin: 1rem;
            box-shadow: 0 0 1rem $gray;
        }
    }
    .galery-2{
        height: 33rem;
        width: 100rem;
        display: flex;
        justify-content: space-between;
        margin-bottom: 20rem;
        picture{
            margin-left: 2.5rem;
            margin-right: 2.5rem;
            img {
                border-radius: 50% ;
                width: 100%;
                height: 100%;
                margin: 1rem;
                box-shadow: 0 0 1rem $gray;
            }
        }
    }
    .info-text {
        margin-top: 5rem;
        text-align: left;
        p{
            margin-bottom: 2.5rem;
        }
    }
}