.hero-refer {
    width: 100vw;
    height: 50vh;
    background-image: url(/build/img/refer-hero.jpg);
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    position: relative;
    overflow: hidden;
    text-align: center;
    .color {
        padding-top: 16rem;
        width: 100%;
        height: 100%;
        background-color: $blueT;
        
        .textos {
            h1,p {
                color: $white;
            }
            h1 {
                letter-spacing: 3rem;
                font-size: 5.5rem;
                font-weight: $black-f;
            }
            p {
                letter-spacing: 1rem;
                font-size: 2rem;
            }
        }
    }
}
.referencias{
    display: flex;
    .testimonial {
        background-color: $gray;
        font-size: 2rem;
        padding: 1rem;
        color: $white;
        border-radius: 2rem;
        margin: 1rem;
        width: 38rem;
        height: 55rem;
        blockquote {
            padding-left: 5rem;
            position: relative;
            &::before {
                content: '';
                background-image: url(/build/img/comilla.svg);
                background-repeat: no-repeat;
                width: 5rem;
                height: 6rem;
                display: block;
                position: absolute;
                left: -2rem;
            }
        }
        p {
            text-align: right;
            color: $white;
        }
        
    }
}
.trabajos {
    display: flex;
    padding: 2rem 0;
    .columna{
        flex: 20%;
        max-width: 38rem;
        margin: 1rem;
        picture{
            img {
                margin: 1rem ;
                filter: grayscale(100%);
                transition: all .3s ease;
                &:hover{
                    cursor: pointer;
                    filter: grayscale(0%);
                    box-shadow: 0 0 1rem $blackT;
                    transform: scale(1.1);
                }
            }
        }

    }
}