.hero-serv {
    width: 100vw;
    height: 50vh;
    background-image: url(/build/img/servicios-hero.jpg);
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    position: relative;
    overflow: hidden;
    text-align: center;
    .color {
        padding-top: 16rem;
        width: 100%;
        height: 100%;
        background-color: $blueT;
        
        .textos {
            h1,p {
                color: $white;
            }
            h1 {
                letter-spacing: 3rem;
                font-size: 5.5rem;
                font-weight: $black-f;
            }
            p {
                letter-spacing: 1rem;
                font-size: 2rem;
            }
        }
    }
}

.container-items{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    .item{
        border-radius: 10px;

        &:hover{
            box-shadow: 0 10px 20px rgba(0, 0, 0, 0.20);
        }
    }
    .info-product{
        padding: 15px 30px;
        line-height: 2;
        display: flex;
        flex-direction: column;
        gap: 10px;
        .price{
            font-size: 18px;
            font-weight: 900;
        }
    }
}





