body {
  font-family: "Rajdhani", sans-serif;
  font-size: 1.8rem;
  line-height: 1.8;
}

p {
  color: #000;
  letter-spacing: 0.23rem;
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0px auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
}

.icon-cart {
  width: 40px;
  height: 40px;
  stroke: #000;
}

.icon-cart:hover {
  cursor: pointer;
}

h1, h2, h3, h4 {
  color: #000;
  font-family: "Rajdhani", sans-serif;
  font-weight: 700;
  text-align: center;
  letter-spacing: 0.3rem;
  margin: 0;
}

label {
  color: #fff;
  letter-spacing: 0.2rem;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.5rem;
}

h4 {
  font-size: 1.8rem;
}

@media (max-width: 480px) {
  p {
    font-size: 1.4rem;
  }
  h1 {
    font-size: 3rem;
  }
  h2 {
    font-size: 2.8rem;
  }
  h3 {
    font-size: 2.2rem;
  }
  h4 {
    font-size: 1.6rem;
  }
}
@media (max-width: 360px) {
  p {
    font-size: 1.2rem;
  }
  h1 {
    font-size: 2.8rem;
  }
  h2 {
    font-size: 2.6rem;
  }
  h3 {
    font-size: 2rem;
  }
  h4 {
    font-size: 1.4rem;
  }
}
@media (max-width: 320px) {
  p {
    font-size: 1rem;
  }
  h1 {
    font-size: 2.6rem;
  }
  h2 {
    font-size: 2.4rem;
  }
  h3 {
    font-size: 1.8rem;
  }
  h4 {
    font-size: 1.2rem;
  }
}
.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.sombra {
  box-shadow: 0px 1px 15px 0px rgba(15, 15, 14, 0.9);
}

.red_sombra {
  box-shadow: 0px 1px 15px 0px rgba(135, 17, 17, 0.8);
}

.label-b {
  color: #000;
}

.boton {
  display: block;
  font-family: "Rajdhani", sans-serif;
  color: #fff;
  text-align: center;
  padding: 1rem 2rem;
  font-size: 1.5rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 2rem;
  border: none;
}
.boton:hover {
  cursor: pointer;
}

.pading-top {
  padding-top: 8rem;
}

.pading_top_2 {
  padding-top: 11rem;
}

.boton-contacto {
  background-color: #414656;
  color: #fff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 1.5rem;
  text-align: center;
  display: inline-block;
  border: none;
  cursor: pointer;
  letter-spacing: 0.2rem;
}
.boton-contacto:hover {
  background-color: #363a47;
}

.boton-con {
  background-color: #06417c;
  color: #fff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 1.5rem;
  text-align: center;
  display: inline-block;
  border: none;
  cursor: pointer;
  letter-spacing: 0.2rem;
}
.boton-con:hover {
  background-color: #053464;
}

.btn-add-cart {
  background-color: #06417c;
  color: #fff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 1.5rem;
  text-align: center;
  display: block;
  border: none;
  cursor: pointer;
  letter-spacing: 0.3rem;
}
.btn-add-cart:hover {
  background-color: #053464;
}

.boton-formularios {
  background-color: #414656;
  color: #fff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 1.5rem;
  text-align: center;
  display: inline-block;
  border: none;
  cursor: pointer;
}
.boton-formularios:hover {
  background-color: #363a47;
}

/** Ui **/
header {
  height: 8rem;
  width: 100%;
  background-color: rgba(6, 65, 124, 0.5);
  backdrop-filter: blur(1rem);
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  z-index: 200;
  top: 0;
}
header .logo {
  height: 70px;
  width: auto;
  margin-left: 5rem;
}
header .logo img {
  max-height: 100%;
  max-width: 100%;
}
header .navegacion {
  justify-content: space-between;
}
header .navegacion a {
  font-size: 2.5rem;
  margin: 0rem 2rem;
  color: #fff;
}
header .navegacion a:hover {
  transform: scale(1.2);
  color: #414656;
  transition: all 300ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
  box-shadow: 0 0 1rem rgba(255, 255, 255, 0.5);
  border-radius: 0.5rem;
}

.container-icon {
  position: relative;
  margin-right: 3rem;
}

.count-products {
  position: absolute;
  top: 55%;
  right: 0;
  background-color: #fff;
  color: #000;
  width: 25px;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}

#contador-productos {
  font-size: 12px;
}

.container-cart-products {
  position: absolute;
  top: 50px;
  right: 0;
  background-color: #A6ABBD;
  width: 400px;
  z-index: 1;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

.cart-product {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.info-cart-product {
  display: flex;
  justify-content: space-between;
  flex: 0.8;
}

.titulo-producto-carrito {
  font-size: 20px;
}

.precio-producto-carrito {
  font-weight: 700;
  font-size: 20px;
  margin-left: 10px;
}

.cantidad-producto-carrito {
  font-weight: 400;
  font-size: 20px;
}

.icon-close {
  width: 25px;
  height: 25px;
}

.icon-close:hover {
  stroke: red;
  cursor: pointer;
}

.cart-total {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  gap: 20px;
}

.cart-total h3 {
  font-size: 20px;
  font-weight: 700;
}

.total-pagar {
  font-size: 20px;
  font-weight: 900;
}

.hidden-cart {
  display: none;
}

footer {
  background-color: #A6ABBD;
  width: 100vw;
  height: 8rem;
  display: flex;
  margin-top: 5rem;
}
footer p {
  margin-left: 15rem;
  justify-content: center;
}

/** internas **/
.hero-index {
  width: 100vw;
  height: 50vh;
  background-image: url(/build/img/index-hero.jpg);
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  position: relative;
  overflow: hidden;
  text-align: center;
}
.hero-index .color {
  padding-top: 16rem;
  width: 100%;
  height: 100%;
  background-color: rgba(6, 65, 124, 0.5);
}
.hero-index .color .textos h1, .hero-index .color .textos p {
  color: #fff;
}
.hero-index .color .textos h1 {
  letter-spacing: 3rem;
  font-size: 5.5rem;
  font-weight: 700;
}
.hero-index .color .textos p {
  letter-spacing: 1rem;
  font-size: 2rem;
}

