  flex: 20%;
  max-width: 38rem;
  margin: 1rem;
}
.trabajos .columna picture img {
  margin: 1rem;
  filter: grayscale(100%);
  transition: all 0.3s ease;
}
.trabajos .columna picture img:hover {
  cursor: pointer;
  filter: grayscale(0%);
  box-shadow: 0 0 1rem rgba(0, 0, 0, 0.5);
  transform: scale(1.1);
}

