.info .form-cont {
  width: 120rem;
  margin: 1rem;
}
.info .form-cont .formulario {
  width: 90%;
  height: 95%;
  padding: 2rem;
  background-color: #A6ABBD;
  border-radius: 1rem;
}
.info .form-cont .formulario .campo {
  display: flex;
  margin-bottom: 2rem;
  gap: 2rem;
  color: #000;
}
.info .form-cont .formulario .campo .campo_label {
  flex: 0 0 9rem;
  text-align: right;
}
.info .form-cont .formulario .campo input {
  display: block;
  height: 5rem;
  width: 100%;
  background-color: #414656;
  border-radius: 0.3rem;
  padding: 0 1rem;
  margin-top: 0.8rem;
  color: #A6ABBD;
}
.info .form-cont .formulario .campo input:focus {
  border: 1px solid #A6ABBD;
  box-shadow: 0 0 1rem #06417c;
  color: #fff;
}
.info .form-cont .formulario .campo .campo_field {
  flex: 1;
  outline: none;
  border: none;
}
.info .form-cont .formulario .campo .campo_field-textarea {
  height: 20rem;
  width: 100%;
  background-color: #414656;
  border-radius: 0.3rem;
  padding: 0 1rem;
  margin-top: 0.8rem;
}
.info .form-cont .formulario .campo .campo_field-textarea:focus {
  border: 1px solid rgba(0, 0, 0, 0.5);
  box-shadow: 0 0 1rem #06417c;
  color: #fff;
}
.info .form-cont .formulario .campo .campo_field-textarea::placeholder {
