.hero-index {
    width: 100vw;
    height: 50vh;
    background-image: url(/build/img/index-hero.jpg);
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    position: relative;
    overflow: hidden;
    text-align: center;
    .color {
        padding-top: 16rem;
        width: 100%;
        height: 100%;
        background-color: $blueT;
        
        .textos {
            h1,p {
                color: $white;
            }
            h1 {
                letter-spacing: 3rem;
                font-size: 5.5rem;
                font-weight: $black-f;
            }
            p {
                letter-spacing: 1rem;
                font-size: 2rem;
            }
        }
    }
}
.dudas {
    text-align: center;
    align-items: center;
    margin-bottom: 5rem;
}
.info {
    .galery{
        height: 38rem;
        display: flex;
        picture{
            img {
                width: 95%;
                height: 90%;
                box-shadow: 0 0 1rem $gray;
            }
 
        }
    }
    p{
        margin-top: 5rem;
        margin-bottom: 5rem;
    }
    iframe {
        align-items: center;
        margin: auto;
        display: block;
    }
}
